<script setup>
import { defineProps, computed } from 'vue';

const props = defineProps({
  setActiveGenre: Function,
  images: Array,
});

const uniqueGenres = computed(() => {
  return [...new Set(props.images.map((item) => item.genre))];
});
</script>

<template>
  <select class="select" @change="setActiveGenre">
    <option disabled>Genres</option>
    <option value="All">All</option>
    <option v-for="genre in uniqueGenres" :key="genre" :value="genre">
      {{ genre }}
    </option>
  </select>
</template>

<style scrope></style>
