<script setup>
import ImageCard from '@/components/ImageCard.vue';
import ImageModal from '@/components/ImageModal.vue';
import FiltersC from '@/components/FiltersC.vue';
import HeaderComponent from '@/components/HeaderComponent.vue';

import { fetchData } from '@/api/api';
import { BASE_URL } from '@/constants';

import { ref, onMounted, computed } from 'vue';

const images = ref([]);

const isLoading = ref(true);

const isOpen = ref(false);
const isFiltersOpen = ref(true);

const activeAuthor = ref('All');

const setActiveAuthor = (e) => {
  activeAuthor.value = e.target.value;
};

const activeGenre = ref('All');

const setActiveGenre = (e) => {
  activeGenre.value = e.target.value;
};

const imageUrl = ref('');

const openFilters = () => {
  isFiltersOpen.value = !isFiltersOpen.value;
};

const updateData = (value, src) => {
  isOpen.value = value;
  imageUrl.value = src;

  if (isOpen.value) {
    document.body.style = 'overflow-y: hidden';
  } else {
    document.body.style = 'overflow-y: scroll';
  }
};

const searchQuery = ref('');

const handleSearchInput = (newValue) => {
  searchQuery.value = newValue;
};

const queryImages = computed(() => {
  return images.value.filter((obj) => {
    return (
      (obj.author.toLowerCase().indexOf(searchQuery.value.toLowerCase()) !==
        -1 ||
        obj.genre.toLowerCase().indexOf(searchQuery.value.toLowerCase()) !==
          -1) &&
      (activeAuthor.value !== 'All'
        ? activeAuthor.value == obj.author
        : true) &&
      (activeGenre.value !== 'All' ? activeGenre.value == obj.genre : true)
    );
  });
});

onMounted(async () => {
  try {
    isLoading.value = true;
    const data = await fetchData(BASE_URL);
    images.value = data;
  } catch (error) {
    console.error(error);
  } finally {
    isLoading.value = false;
  }
});
</script>

<template>
  <HeaderComponent :clickA="openFilters" />
  <main class="main">
    <div class="container">
      <aside class="aside" :class="{ 'aside-hidden': !isFiltersOpen }">
        <FiltersC
          :setActiveAuthor="setActiveAuthor"
          :setActiveGenre="setActiveGenre"
          :images="images"
          :searchQuery="searchQuery"
          @searchInput="handleSearchInput"
          v-show="isFiltersOpen"
        />
      </aside>
      <section class="content">
        <div class="list">
          <ImageCard
            :updateParentData="updateData"
            :isOpen="isOpen"
            v-for="image in queryImages"
            :key="image.id"
            :obj="image"
          />
        </div>
        <div class="loading" v-if="isLoading">Loading...</div>
        <p class="no-images" v-if="queryImages.length < 1 && !isLoading">
          No similar images found
        </p>
        <ImageModal
          :imageUrl="imageUrl"
          :updateParentData="updateData"
          :isOpen="isOpen"
        />
      </section>
    </div>
  </main>
</template>

<style scoped>
.main {
  height: 100%;
}
.main .container {
  display: flex;
}

.aside {
  min-height: 100%;
  max-width: 300px;
  padding: 40px 20px;
  padding-left: 0;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
}

.content {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: center;
  flex-direction: column;
  padding: 40px 0;
  margin: 0 auto;
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.loading,
.no-images {
  font-size: 28px;
}
</style>
