<script setup>
import UIInput from './UIInput.vue';
import AuthorSelect from './AuthorSelect.vue';
import GenreSelect from './GenreSelect.vue';

import { defineProps, defineEmits } from 'vue';

const props = defineProps({
  setActiveAuthor: Function,
  setActiveGenre: Function,
  setActive: Function,
  images: Array,
  searchQuery: String,
});

const emit = defineEmits(['searchInput']);

const onSearchInput = (event) => {
  emit('searchInput', event.target.value);
};
</script>

<template>
  <div class="filters">
    <UIInput
      class="search"
      :model-value="searchQuery"
      :value="props.searchQuery"
      @input="onSearchInput"
    />
    <AuthorSelect :setActiveAuthor="setActiveAuthor" :images="images" />
    <GenreSelect :setActiveGenre="setActiveGenre" :images="images" />
  </div>
</template>

<style scoped>
.search {
  margin-bottom: 20px;
}
</style>
